import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { TokenInterceptorService } from '../app/services/token-interceptor.service';

import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { MatTabsModule, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MatDialogModule } from '@angular/material';
import { MatIconModule } from '@angular/material';
import { MatChipsModule } from '@angular/material';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatAutocompleteModule } from '@angular/material';
import { MatExpansionModule } from '@angular/material';
import { MatInputModule } from '@angular/material';
import { MatSelectModule } from '@angular/material/select';
import { MatProgressSpinnerModule } from '@angular/material';
import { MatSliderModule } from '@angular/material';
import { MatNativeDateModule,MatDatepickerModule, MatRippleModule } from '@angular/material';
import { MatSidenavModule } from '@angular/material';
import { MatButtonToggleModule } from '@angular/material';
import { MatButtonModule } from '@angular/material/button';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LazyLoadImageModule } from 'ng-lazyload-image';
import { QuillModule } from 'ngx-quill/bundles';
import { OwlModule } from 'ngx-owl-carousel';

import { MainPipe } from '../app/main-pipe.module';

import { NguiInViewComponent } from '../app/ngui-in-view.component';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { SideNavComponent } from './side-nav/side-nav.component';
import { RightNavComponent } from './right-nav/right-nav.component';
import { PostNavComponent } from './post-nav/post-nav.component';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { LoginService } from './services/login.service';
import { AuthGuard } from './guards/auth.guard';
import { ProfileComponent } from './profile/profile.component';
import { PostComponent } from './post/post.component';
import { ContestComponent, CreateContestDialog, SubmitEntry } from './contest/contest.component';
import { UnauthUserGuard } from './guards/unauth-user.guard';
import { UploadComponent } from './upload/upload.component';
import { CommreplyComponent } from './commreply/commreply.component';
import { SingleContestComponent } from './contest/single-contest/single-contest.component';
import { SubmissionComponent } from './contest/submission/submission.component';
import { Notify } from './login/login.component';
import { SinglePostComponent } from './single-post/single-post.component';

const ROUTES: Routes=[
  {path:'',redirectTo:'home/Meme/toprated',pathMatch:'full'},
  // {path: 'contest/id/:conid/:id',component:SubmissionComponent},
  {path:'home',redirectTo:'home/Meme/toprated',pathMatch:'full',},
  {path:'home/:category',redirectTo:'home/:category/toprated',pathMatch:'full'},
  {path:'home/:category/:filter',component:HomeComponent},
  {path:'post/:pid',component:SinglePostComponent},
  {path:'profile/:user',component:ProfileComponent,canActivate:[UnauthUserGuard]},
  {path:'contest/id/:conid',component:SingleContestComponent,canActivate:[UnauthUserGuard]},
  {path:'contests',component:ContestComponent,canActivate:[UnauthUserGuard]},
  {path:'upload',component:UploadComponent,canActivate:[UnauthUserGuard]},
  {path:'login',component:LoginComponent,canActivate:[AuthGuard]},
  {path:'**',redirectTo:'home/Meme/toprated',pathMatch:'full'}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    SideNavComponent,
    RightNavComponent,
    PostNavComponent,
    CreateContestDialog,
    Notify,
    SubmitEntry,
    ContestComponent,
    HomeComponent,
    LoginComponent,
    ProfileComponent,
    PostComponent,
    ContestComponent,
    UploadComponent,
    NguiInViewComponent,
    CommreplyComponent,
    SingleContestComponent,
    SubmissionComponent,
    SinglePostComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    BrowserAnimationsModule,
    HttpModule,
    MatChipsModule,
    MatExpansionModule,
    MatInputModule,
    MatIconModule,
    MatSidenavModule,
    MatSnackBarModule,
    MatSelectModule,
    HttpClientModule,
    MatProgressSpinnerModule,
    MatTabsModule,
    MatAutocompleteModule,
    MatSliderModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatDatepickerModule,
    MatNativeDateModule,MatDatepickerModule, MatRippleModule,
    MatDialogModule,
    LazyLoadImageModule,
    ReactiveFormsModule,
    QuillModule,
    OwlModule,
    MainPipe,
    RouterModule.forRoot(ROUTES,{useHash:true})
  ],
  entryComponents: [CreateContestDialog,
                    SubmitEntry,
                    Notify,
                    SubmissionComponent],
  providers: [LoginService,AuthGuard,UnauthUserGuard,
  {
    provide: HTTP_INTERCEPTORS,
    useClass: TokenInterceptorService,
    multi: true
  },{ provide: MatDialogRef, useValue: {} },
  { provide: MAT_DIALOG_DATA, useValue: [] },],
  bootstrap: [AppComponent]
})
export class AppModule { }
