import { Component, OnInit, Input ,ViewChild,Inject , ElementRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ContestsService } from 'src/app/services/contests.service';
import { LoginService } from 'src/app/services/login.service';
import { MatSnackBar,MatDialog, MatDialogRef,MAT_DIALOG_DATA } from '@angular/material';
import { async } from '@angular/core/testing';
import * as moment from 'node_modules/moment';

@Component({
  selector: 'app-submission',
  templateUrl: './submission.component.html',
  styleUrls: ['./submission.component.css']
})
export class SubmissionComponent implements OnInit {

  // @Input() sub;
  // @Input() coms:Object;
  // @Input() conid;
  
  allcomments:Array<Object>;

  private getaImageURL="";
  avatar:String;

  showComm:Boolean=false;
  showreply:Boolean=false;
  showreplyfrm:Boolean=false;
  toreply:String="";
  focusit:Boolean=false;
  curimg:Number=0;

  rcount:Number=0;
  rvalue:Number=0;
  rtotal:Number=0;
  rprev:Number=0;

  constructor(private route:ActivatedRoute,
              private router : Router,
              private conserve:ContestsService,
              private loginservice:LoginService,
              private dialogRef:MatDialogRef<SubmissionComponent>,
              private snackbr:MatSnackBar,
              @Inject(MAT_DIALOG_DATA) public data) { 
    this.avatar = localStorage.getItem('avatar');
    // console.log(this.data.n);
  }

  ngOnInit() {
    
  }

  moveSub(type){
    if(type===1)
      var np = (parseInt)(this.data.n)-1;
    else if(type===0)
      var np = (parseInt)(this.data.n)+1;
      
    this.dialogRef.close("closed");
    this.router.navigate(
      [], 
      {
        relativeTo: this.route,
        queryParams: { 'submission': np},
        queryParamsHandling: 'merge',
        replaceUrl: true
      });
  }

  frmNow(d){
    return moment(d).startOf('minute').fromNow();
  }

  getContestImages(imgpath){
    if(imgpath != undefined)
    //console.log(imgpath);
     return (this.getaImageURL+imgpath) || async;
  }

  showComments(comms){
    this.showComm = !this.showComm;
    this.allcomments = comms.sort((a,b)=> {return <any>new Date(b.time) - <any>new Date(a.time);});
  }

  getProfimg(imgpath){
    if(imgpath != undefined)
     return (this.getaImageURL+imgpath) || async; 
  }


  sendComment(comfrm,postid,comms){
     //console.log(comfrm.value.cominput);
    if(comfrm.value.cominput == null){
        this.snackbr.open("Enter text", "Cancel", {
          duration: 800,
        });
      }
    else  if(comfrm.value.cominput.trim()==""){
      this.snackbr.open("Enter text", "Cancel", {
        duration: 800,
      });
      comfrm.reset();
      }
    else{
      let combody:Object = {
          p_id:postid,
          cbody:comfrm.value.cominput
      };
      if(!this.showComm)
        this.showComments(comms);
      
      //here will api subscribe
      this.conserve.addComment(combody).subscribe(res =>{
        if(res.message === "Comment added"){
          comfrm.reset();
          this.allcomments.unshift(res.nc);
          this.snackbr.open("Comment added", "Ok", {
            duration: 700,
          });

        }else{
          
          this.snackbr.open(res.message, "Cancel", {
            duration: 700,
          });
        }
      });
    }
  }


  newTR(tr,rv){
      return this.rtotal = tr;
  }

  newRC(rc,rv){
      return this.rcount = rc;
  }

  totalRate(list){
   if(list.length==0){
     this.rtotal = this.rvalue;
   }
   else{
     var rv,sum=0;
     list.forEach(el => {
      rv = parseInt (el.value)
       sum = sum + rv;
     });

     this.rtotal = sum;
    //console.log(sum);
    //console.log(list);
   }
    
       
  }

  rateIt(sid,cid,whorated){

    this.totalRate(whorated);
    var r = new Object({
        total: this.rtotal,
        ratecount:whorated.length,
    });
    // this.countRate(rating.ratecount);
    // this.totalRate(rating.total,rating.prevrate,rating.ratecount);
    //console.log(cid);
    if(this.rvalue>=1){
    this.conserve.ratePost(sid,cid,this.rvalue,r).subscribe(res=>{
      if(res.message){
        this.snackbr.open("You have Rated the post", "Ok", {
          duration: 800,
        });
        console.log(res.rating);
        this.newRC(res.rating.ratecount,this.rvalue);
        this.newTR(res.rating.total,this.rvalue);
        this.ngOnInit();
      }
      else{
        //console.log(res);
        this.snackbr.open("Something went wrong", "Cancel", {
          duration: 800,
        }); 
      }
    });

    this.rvalue=0;
  }

    //after reset;

  }

}
