import { Component, OnInit } from '@angular/core';
import { PostRService } from '../services/post-r.service';
import { ActivatedRoute, Router,RouterEvent, NavigationStart, NavigationEnd, NavigationCancel } from '@angular/router';

@Component({
  selector: 'app-single-post',
  templateUrl: './single-post.component.html',
  styleUrls: ['./single-post.component.css']
})
export class SinglePostComponent implements OnInit {

  showLoadingIndicator:Boolean=true;
  msg:String;
  postdetails:Array<Object>;

  constructor(private pserv:PostRService,
              private router: Router,
              private aroute:ActivatedRoute) {
                this.aroute.params.subscribe(params => {
                  this.pserv.getSinglePost(params['pid']).subscribe(res=>{
                    if(res.message){
                      this.postdetails = res.post;
                      this.showLoadingIndicator = false;
                    }else{
                      this.msg="Post Not Available";
                      console.log(this.msg);
                    }
                  })
                });
   }

  ngOnInit() {
  }

}
