import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {FormControl, FormGroup, Validators } from '@angular/forms';
import {Observable} from 'rxjs';
import {MatAutocompleteSelectedEvent, MatChipInputEvent, MatAutocomplete} from '@angular/material';
import {map, startWith} from 'rxjs/operators';
import { UserinfoService } from '../services/userinfo.service';
import { LoginService } from '../services/login.service';
import { Router } from '@angular/router';
import { PostRService } from '../services/post-r.service';

import { QuillEditorComponent } from 'ngx-quill/bundles';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  separatorKeysCodes: number[] = [ENTER, COMMA];
  formupload = new FormGroup({
    category: new FormControl('',[Validators.required]),
    divisionCtrl: new FormControl('',[Validators.required]),
    attachment: new FormControl('',[Validators.required]),
    description: new FormControl('',[Validators.required]),
    title: new FormControl('',[Validators.required]),
    hashtags: new FormControl('',[Validators.required])
  });
  
  catoptions:String[]=['Meme', 'Art','Music','Writing','Lifestyle','Other'];

  filterDivision:Observable<String[]>;

  divs:String[]=[];
  alldivs:String[]=['WTF', 'LOL','Trolls',
  'School',
  'Sci-fi',
  'Sports',
  'Tik-Tok',
  'Animals',
  'Relationship',
  'Gaming',
  'Movies & Music',
  'Anime',
  'Cosplay',
  'K-Pop',
  'Vintage',
  'Drawing',
  'Digital-Art',
  'Painting',
  'Design',
  'Illustrations',
  'Templates',
  'Crafts',
  'Calligraphy',
  'Animation',
  'Photography',
  'Comics',
  'Classic',
  'Country',
  'Dance',
  'Electronic',
  'Hip Hop',
  'Jazz',
  'Latin',
  'Opera',
  'R&B',
  'Rock',
  'Stories',
  'Poems',
  'Quotes',
  'Blogs',
  'Reviews',
  'Confessions',
  'Culture',
  'Fashion',
  'Food & Drinks',
  'Health & Fitness',
  'Tourism',
  'Decorating'];

  selectedFile:Array<File>=[];
  imgSrc=[];

  openTab:Boolean=true;
  avfilename:Object;
  avstr:String;
  salestr:String;
  salefilename:Object;

  public tbr={
    toolbar: [
      ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
      ['blockquote', 'code-block'],

      [{ 'list': 'ordered'}, { 'list': 'bullet' }],
      [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
      // [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
      // [{ 'direction': 'rtl' }],                         // text direction
  
      // [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
      [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
  
      [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
      // [{ 'font': [] }],
      [{ 'align': [] }],
  
      // ['clean'],                                         // remove formatting button
  
      ['link', 'image', 'video']                         // link and image, video
    ]
  };
  editordata:String;

  @ViewChild('editor') editor: QuillEditorComponent;
  @ViewChild('DivisionInput') divisionInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;


  constructor( private userserve:UserinfoService,
               private ls: LoginService,
               private ups: PostRService,
               private router: Router) 
    {
      //check-Auth
      this.userserve.getauser().subscribe(res=>{
          if(res.message === "Auth Failed"){
            console.log(res.message);
            this.ls.logoutUser();
            this.router.navigate(['/login']);
          }
          console.log(res.message);
      });

      // filtering the divisions
      this.filterDivision = this.formupload.controls.divisionCtrl.valueChanges.pipe(
        startWith(null),
        map((d: string | null) => d ? this.Dfilter(d) : this.alldivs.slice()));

        
   }

   setDivs(index){
    this.divs=[];
    let adiv:String[]; 
    adiv = this.alldivs;
     // filtering the divisions
     this.filterDivision = this.formupload.controls.divisionCtrl.valueChanges.pipe(
      startWith(null),
      map((d: string | null) => d ? this.Dfilter(d) : adiv.slice()));
   }

   private Dfilter(value: string): String[] {
     if(value){
    value = value.toString().toLowerCase();
    console.log(value);
    return this.alldivs.filter((option:String) => option.toLowerCase().indexOf(value) === 0);
     }
  }

  add(event: MatChipInputEvent): void {
    // Add division only when MatAutocomplete is not open
    // To make sure this does not conflict with OptionSelected Event
    if (!this.matAutocomplete.isOpen) {
      const input = event.input;
      const value = event.value;

      // Add our divisions
      if ((value || '').trim()) {
        this.divs.push(value.trim());
      }

      // Reset the input value
      if (input) {
        input.value = '';
      }

      this.formupload.controls.divisionCtrl.setValue(null);
    }
  }

  remove(fruit: string): void {
    const index = this.divs.indexOf(fruit);

    if (index >= 0) {
      this.divs.splice(index, 1);
    }
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    if(!this.divs.includes(event.option.viewValue)){
      this.divs.push(event.option.viewValue);
      console.log('in if'+this.divs);
    }
    console.log(this.divs);
    this.divisionInput.nativeElement.value = '';
    this.formupload.controls.divisionCtrl.setValue(null);
  }



  //form upload function
  upload(){
    this.formupload.controls.divisionCtrl.setValue(this.divs);
    //uploadURL='http://localhost:3000/posts/add';
    let fd = new FormData();
        fd.append('category',this.formupload.value.category);
        fd.append('division',this.formupload.value.divisionCtrl);
        fd.append('bodyText',this.formupload.value.description);
        fd.append('title',this.formupload.value.title);
        fd.append('hashtags',this.formupload.value.hashtags);
        if(this.selectedFile){
        for(let i =0; i < this.selectedFile.length; i++){
          fd.append("postMedia[]", this.selectedFile[i], this.selectedFile[i]['name']);
        }
      }
    //console.log(this.formupload.value);
    this.ups.uploadPost(fd).subscribe((res)=>{
      //console.log(res);
      if(res.message=="Created"){
        this.formupload.reset();
        this.router.navigate(['/post',res.post._id]);
      }else{
        
      }
    });
  }


  selectPostImg(event){
    if (event.target.files) {
      var fs = event.target.files;
      
      for (var i=0;i<fs.length;i++) {
        var reader = new FileReader();
        //console.log(i);
        reader.onload = (e:any) => { // called once readAsDataURL is completed
          if(this.imgSrc.length < 6)
            this.imgSrc.push(e.target.result);
          //console.log(e.target);
        }

        reader.readAsDataURL(fs[i]); // read file as data url
        if(this.selectedFile.length < 6)
          this.selectedFile.push(<File> fs[i]);
      }
      console.log(this.selectedFile);
    }
  }

  getavFilename(event){
        this.avfilename = event.target.files[0];
        this.avstr = event.target.files[0].name;
  }
  getsaleFilename(event){
    this.salefilename = event.target.files[0];
    this.salestr = event.target.files[0].name;
}
  openAv(){
    this.openTab = true;
    // this.avfilename={};
  }
  openSale(){
    this.openTab = false;
    // this.salefilename = {};
  }
  ngOnInit() {

    this.editor
        .onContentChanged
        .pipe(
          debounceTime(400),
          distinctUntilChanged()
        )
        .subscribe((data) => {
          this.editordata=data.html;
          console.log(this.editordata);
        });  
    
    
    }

    showIndex(i){
      this.selectedFile.splice(i,1);
      this.imgSrc.splice(i,1);
  }

}
