import { Component, OnInit,ElementRef, AfterViewInit, AfterContentChecked,Injectable,Inject,ViewChild, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ContestsService } from 'src/app/services/contests.service';
import { LoginService } from 'src/app/services/login.service';
import { SubmitEntry } from "../contest.component"
import { MatSnackBar,MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { async } from '@angular/core/testing';
import { Router } from '@angular/router';
import { FormGroup,FormControl,Validators } from "@angular/forms";
import * as moment from 'node_modules/moment';
import { SubmissionComponent } from '../submission/submission.component';
import { Subscription } from 'rxjs';
import { Overlay } from '@angular/cdk/overlay';


@Component({
  selector: 'app-single-contest',
  templateUrl: './single-contest.component.html',
  styleUrls: ['./single-contest.component.css']
})
export class SingleContestComponent implements OnInit {

  enumber:String="1";
  condata:Object;
  subs:Object;
  routeQueryParams$: Subscription;

  curimg:Number = 0;
  btndis:Boolean=true;
  private getaImageURL="";
  avatar:String;

  showComm:Boolean=false;
  showreply:Boolean=false;
  showreplyfrm:Boolean=false;
  toreply:String="";
  focusit:Boolean=false;

  rcount:Number=0;
  rvalue:Number=0;
  rtotal:Number=0;
  rprev:Number=0;

  opvalue:Number=2;

  allcomments:Array<Object>;

  @ViewChild("comarea") commentField:ElementRef;

  constructor(private route:ActivatedRoute,
              private router:Router,
              public dialog: MatDialog,
              private conserve:ContestsService,
              private loginservice:LoginService,
              private snackbr:MatSnackBar) { 
    this.avatar = localStorage.getItem('avatar');
    this.route.params.subscribe(params => {
            //subscribe to a perticular contest
            this.conserve.getSingleContest(params['conid']).subscribe(res=>{
              this.condata = res.contests;
              this.subs = res.contests[0].submissions;

              // open dialog of submission[x-1]
              this.routeQueryParams$ = route.queryParams.subscribe(params => {
                if (params['submission']) {
                  if(this.subs[params['submission']-1]){
                    this.openEntryDetails(this.subs[params['submission']-1],params['conid'],params['submission']);
                  }else{
                    this.router.navigate(['.'], { relativeTo: this.route });
                    this.dialog.closeAll();
                  }
                }
              });
              
            });
    });
  }

  ngOnInit() {
  }
  ngOnDestroy() {
    this.routeQueryParams$.unsubscribe();
  }
  //opening Entry form dialog
  openEntryDialog(id,name): void {
    const subentry = this.dialog.open(SubmitEntry, {
      width: '50%',
      data: {conid: id,cname: name}
    });

    subentry.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
      // this.animal = result;
    });
  }

  openEntryDetails(num,id,x){
    const sub = this.dialog.open(SubmissionComponent,{
      width:'70%',
      autoFocus: false,
      data : {subdetails:num,conid: id,n:x}
    });

    sub.afterClosed().subscribe(result => {
      if(result!="closed")
        this.router.navigate(['.'], { relativeTo: this.route });

    });
  }

  navigateSub(x,conid){
    this.router.navigate(['contest','id',conid],{queryParams:{submission:x}});
  }

  seewat(e){
    if(e==0){
      return true;
    }
    else return false;
  }

  getlength(subs){
      return subs.length;
  }

  dateStr(sd,ed,ev){
    var returnstr="";
    var d=""
    var curdate:any =  new Date();
    var strdate:any = new Date(sd);
    var enddate:any = new Date(ed);
    var evalv:any = new Date(ev);
    //console.log(enddate-evalv);
    if((curdate-enddate)<1){
      returnstr = "Open";
      d=sd;
    return returnstr;
    }
    if((curdate-evalv)>=1){
      returnstr = "Evaluation ";
      d=ed;
      this.btndis=false;
    return returnstr;
    }
    if((curdate-enddate)>=1){
      returnstr = "Closed ";
      d=ed;
      this.btndis=false;
    return returnstr;
    }
    //console.log((curdate-strdate));
    //return moment(d).endOf('minute').fromNow();
  }

  conTimePeriod(e){
    return moment(e).format('MMMM Do ');
  }

  frmNow(d){
    return moment(d).startOf('minute').fromNow();
  }

  getContestImages(imgpath){
    if(imgpath != undefined)
    //console.log(imgpath);
     return (this.getaImageURL+imgpath) || async;
  }

  showComments(comms){
    this.showComm = !this.showComm;
    this.allcomments = comms.sort((a,b)=> {return <any>new Date(b.time) - <any>new Date(a.time);});
  }

  getProfimg(imgpath){
    if(imgpath != undefined)
     return (this.getaImageURL+imgpath) || async; 
  }


  sendComment(comfrm,postid,comms){
     //console.log(comfrm.value.cominput);
    if(comfrm.value.cominput == null){
        this.snackbr.open("Enter text", "Cancel", {
          duration: 800,
        });
      }
    else  if(comfrm.value.cominput.trim()==""){
      this.snackbr.open("Enter text", "Cancel", {
        duration: 800,
      });
      comfrm.reset();
      }
    else{
      let combody:Object = {
          p_id:postid,
          cbody:comfrm.value.cominput
      };
      if(!this.showComm)
        this.showComments(comms);
      
      //here will api subscribe
      this.conserve.addComment(combody).subscribe(res =>{
        if(res.message === "Comment added"){
          comfrm.reset();
          this.allcomments.unshift(res.nc);
          this.snackbr.open("Comment added", "Ok", {
            duration: 700,
          });

        }else{
          
          this.snackbr.open(res.message, "Cancel", {
            duration: 700,
          });
        }
      });
    }
  }


  newTR(tr,rv){
      return this.rtotal = tr;
  }

  newRC(rc,rv){
      return this.rcount = rc;
  }

  totalRate(list){
   if(list.length==0){
     this.rtotal = this.rvalue;
   }
   else{
     var rv,sum=0;
     list.forEach(el => {
      rv = parseInt (el.value)
       sum = sum + rv;
     });

     this.rtotal = sum;
    //console.log(sum);
    //console.log(list);
   }
    
       
  }

  rateIt(sid,cid,whorated){

    this.totalRate(whorated);
    var r = new Object({
        total: this.rtotal,
        ratecount:whorated.length,
    });
    // this.countRate(rating.ratecount);
    // this.totalRate(rating.total,rating.prevrate,rating.ratecount);
    //console.log(cid);
    if(this.rvalue>=1){
    this.conserve.ratePost(sid,cid,this.rvalue,r).subscribe(res=>{
      if(res.message){
        this.snackbr.open("You have Rated the post", "Ok", {
          duration: 800,
        });
        console.log(res.rating);
        this.newRC(res.rating.ratecount,this.rvalue);
        this.newTR(res.rating.total,this.rvalue);
        this.ngOnInit();
      }
      else{
        //console.log(res);
        this.snackbr.open("Something went wrong", "Cancel", {
          duration: 800,
        }); 
      }
    });

    this.rvalue=0;
  }
}

  ckopvalue(value){
    if(this.opvalue===value)
      return true;
    else
      return false;  
  }

}
