import { Component, OnInit,Inject } from '@angular/core';
import { LoginService } from '../services/login.service';
import { Router } from "@angular/router";
import { Http,Response,Headers } from '@angular/http';
import { map } from 'rxjs/operators';
import { MatDialog,MAT_DIALOG_DATA,MatDialogRef } from '@angular/material';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers:[LoginService]
})
export class LoginComponent implements OnInit {
  private signupURL = "user/signup";
  selectedFile: File = null;
  divActive:boolean = true;
  imgSrc:String='../../assets/defaultuser.png';
  msg:String;
  msgType:Boolean=true;

  constructor(private loginservice:LoginService,
              private http:Http,
              private dialog:MatDialog,
              private router:Router) { 

              }

  ngOnInit() {
    
  }

  openNotifyDialog(): void {
    const dialogRef = this.dialog.open(Notify, {
      width: 'auto',
      data: {msg: this.msg,msgtype:this.msgType}
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
      // this.animal = result;
      this.msg = "";
    });
  }

  changeUserImg(event){
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]); // read file as data url
      
      this.selectedFile =<File> event.target.files[0];

      reader.onload = (e:any) => { // called once readAsDataURL is completed
        this.imgSrc = e.target.result;
      }
      console.log(this.imgSrc);
    }
  }

  changeDiv(){
    this.divActive = !this.divActive;
  }
  loginFun(form){
    const user={
      email:form.value.email,
      username:form.value.uname,
      password:form.value.password
    };

    this.loginservice.loginUser(user)
        .subscribe(
          res => {
            
            if(res.message==="Auth Successful"){

              localStorage.setItem('token',res.token);
              const time_to_login = Date.now() + 3600000; // one hour
              localStorage.setItem('timer', JSON.stringify(time_to_login));

              localStorage.setItem('avatar',res.user.profimg);
              this.loginservice.user = res.user;
              this.router.navigate(['/home']);
            }
            if(res.message==="Auth Failed"){
              //console.log(res);
              this.msgType=false;
              this.msg="Authentication Failed";
              this.openNotifyDialog();
            }
            //console.log(this.loginservice.user);
          }
        );
        form.reset();
  }

  signupUser(form){
    const user={
      email:form.value.email,
      username:form.value.uname,
      password:form.value.password
    };
    let fd = new FormData();
    let header = new Headers();
    if(this.selectedFile != null){
      fd.append('userProfile',this.selectedFile);
    }
    fd.append('email',user.email);
    fd.append('username',user.username);
    fd.append('password',user.password);
    header.append('Content-type','form');
    
    form.reset();                
    return this.http.post(this.signupURL,fd)
                    .pipe(map(res=> res.json()))                    
                    .subscribe(res=>{
                      console.log(res);
                      if(res.message === "user created")
                          this.changeDiv();
                      else{
                        this.msg=res.message;
                        this.msgType=false;
                        this.openNotifyDialog();
                      }    
                    });
  }
}


