import { AfterViewInit, ViewChild ,Component, OnInit,ElementRef, Input} from '@angular/core';
import { LoginService } from '../services/login.service';
import { async } from '@angular/core/testing';
import { MatSnackBar } from '@angular/material';
import { PostRService } from '../services/post-r.service';
import { _document } from '@angular/platform-browser/src/browser';
import { DOCUMENT } from '@angular/platform-browser';
import { Router } from '@angular/router';
import * as moment from 'node_modules/moment';
import { ContestsService } from '../services/contests.service';

@Component({
  selector: 'app-commreply',
  templateUrl: './commreply.component.html',
  styleUrls: ['./commreply.component.css']
})
export class CommreplyComponent implements OnInit,AfterViewInit {

  showreply:Boolean=false;
  showreplyfrm:Boolean=false;
  avatar:String;
  toreply:String="";
  focusit:Boolean=false;
  private getaImageURL="";

  @Input() allreplies:Array<Object>;
  @Input() c_id:String;
  @Input() touser:Object;
  @Input() type;

  @ViewChild("rinput") inputEl: ElementRef;
  nofor = 0;

  constructor(private loginservice:LoginService,
              private snackbr:MatSnackBar,
              private router:Router,
              private postservice:PostRService, private conserve:ContestsService) { 
    this.avatar = localStorage.getItem('avatar');
    // this.allreplies.sort((val1, val2)=> {return new Date(val1.time) - new 
    //   Date(val2.time)});
   }

  ngAfterViewInit() {
    }
  ngOnInit() {
    }

    frmNow(d){
      return moment(d).startOf('minute').fromNow();
    }
    
  showReplies(reps){
    this.showreply = !this.showreply;
    this.focusit = true;
    this.allreplies = reps.sort((a,b)=> {return <any>new Date(b.time) - <any>new Date(a.time);});
  }

  showreplyform(tou){
    this.showreplyfrm = true;
    this.touser = tou;
    var frm = document.getElementById("rfrm"+this.c_id);
    //console.log(frm);
    if(frm == null){
      this.router.navigate(['/login']);
    }
    else{
      frm.style.display="block"
      document.getElementById("rinp"+this.c_id).focus();
    }
  }

  getProfimg(imgpath){
    if(imgpath != undefined)
     return (this.getaImageURL+imgpath) || async; 
  }

  closereplyform(frm){
    frm.style.display= 'none';
  }

  getlength(rps){
    return rps.length;
  }

  sendReply(rpfrm,comid){
    //console.log(rpfrm.value.rinpt);
    if(rpfrm.value.rinpt == null){
      this.snackbr.open("Enter text", "Cancel", {
        duration: 800,
      });
    }
    else if(rpfrm.value.rinpt.trim()==""){
      this.snackbr.open("Enter text", "Cancel", {
        duration: 800,
      });
    }
    else{
      let combody:Object = {
          c_id:comid,
          tou:this.touser,
          rbody:rpfrm.value.rinpt
      };
      //console.log(combody);
      //here will api subscribe
      if(this.type==1){
      this.postservice.addReply(combody).subscribe(res =>{
        if(res.message === "Reply added"){
          rpfrm.reset();
          this.allreplies.unshift(res.nc);
          this.snackbr.open("Reply added", "Ok", {
            duration: 700,
          });
          //console.log(res.nc);
        }else{
          
          this.snackbr.open(res.message, "Cancel", {
            duration: 700,
          });
        }
      });
    }
    if(this.type==2){
      this.conserve.addReply(combody).subscribe(res =>{
        if(res.message === "Reply added"){
          rpfrm.reset();
          this.allreplies.unshift(res.nc);
          this.snackbr.open("Reply added", "Ok", {
            duration: 700,
          });
          //console.log(res.nc);
        }else{
          
          this.snackbr.open(res.message, "Cancel", {
            duration: 700,
          });
        }
      });
      }
    }
  }


}
