import { Component, OnInit } from '@angular/core';
import { LoginService } from '../services/login.service';
import { PostRService } from '../services/post-r.service';
import { CatdivService } from '../services/catdiv.service';
import { ActivatedRoute, Router,RouterEvent, NavigationStart, NavigationEnd, NavigationCancel } from '@angular/router';


@Component({
  selector: 'app-post-nav',
  templateUrl: './post-nav.component.html',
  styleUrls: ['./post-nav.component.css']
})
export class PostNavComponent implements OnInit {

  //sidenav vars
  division;
  categoryName;
  tractive:String='toprated';
  cparam;
  ifContest:Boolean=false;

  urate:any=1;
  verticle= true;
  curCategory:String;
  curFilter:String;

  showLoadingIndicator:Boolean=true;

  postdetails:Array<Object>;

  constructor(private loginservice:LoginService,
              private aps:PostRService,
              private cdservice: CatdivService,
              private router: Router,
              private aroute:ActivatedRoute) {

          // this.aroute.params.subscribe(params => {
                  
          //     });      

          this.aroute.params.subscribe(params => {
                  //params for sidenav
                  this.cparam = params['category'];
                  this.getDivision(params['category']);
                  this.tractive = params['filter'];

                  //params for posts
                  if(params['filter']==="toprated" || params['filter']==="fresh"){
                    this.curCategory=params['category'].charAt(0).toUpperCase() + params['category'].slice(1);
                    this.curFilter=params['filter'].charAt(0).toUpperCase() + params['filter'].slice(1);                    
                    this.getFilteredPosts(params['category'],params['filter']);
                  }
                  else{
                    this.getDivPosts(params['category'],params['filter']);
                  }
          });
    
   }

  ngOnInit() { }

  getDivision(category:String='Meme'){  
    this.cdservice.getDivisionService(category)
                  .subscribe(
                    res=>{
                      this.division= res.category.division;
                      this.categoryName=res.category.name; 
                      //console.log(this.categoryName);
                    }
                  )
    }


  onCategory(catname:String='Meme'){
      this.ifContest=false;
      
      this.router.navigate([catname,'toprated']);
  }

  goforPosts(filter:string){
    // console.log( this.categoryname+'/'+filter);
    this.router.navigate([this.categoryName,filter]);
  }

  onContests(catname:String){
    this.ifContest=true;
    //this.router.navigate(['home',catname,'toprated']);
  }

  getFilteredPosts(ct,ft){
    this.showLoadingIndicator=true;
    this.aps.getallPosts(ct,ft).subscribe((res) =>{
      if(res){
        console.log(res.posts);
        this.postdetails=res.posts;
      }
      this.showLoadingIndicator=false;
    });
  }

  getDivPosts(ct,ft){
    this.aps.getcdPosts(ct,ft).subscribe(res =>{
      if(res){
        console.log(res.posts);

          this.postdetails = res.posts;
      }
      this.showLoadingIndicator=false;
    });
  }

}
