import { Component, OnInit, Input ,ViewChild , ElementRef ,Renderer } from '@angular/core';
import { LoginService } from '../services/login.service';
import { async } from '@angular/core/testing';
import { MatSnackBar, MatIconRegistry } from '@angular/material';
import { PostRService } from '../services/post-r.service';
import * as moment from 'node_modules/moment';
import {DomSanitizer} from '@angular/platform-browser';


@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})

export class PostComponent implements OnInit {
  
  private getaImageURL="";
  showComm:Boolean=false;
  showreply:Boolean=false;
  showreplyfrm:Boolean=false;
  avatar:String;
  toreply:String="";
  focusit:Boolean=false;
  disabled:Boolean=false;
  dfimg="../../assets/testimg/k.jpg";

  rcount:Number;
  rtotal:Number;
  owlop:Object={
        items:1,
        loop:false,
        margin:10,
        video:true,
        center:true,
        lazyLoad:true,
        onInitialized:'500px',
        nav:true,
        navText : ["",""],
        responsive:{
            480:{
                items:1
            },
            600:{
                items:1
            }
        }
  }

  @Input() spost:Object;

  allcomments:Array<Object>;
  curimg:Number = 0;

  @ViewChild("comarea") commentField:ElementRef;

  constructor(private loginservice:LoginService,
              private snackbr:MatSnackBar,
              private postservice:PostRService,
              sanitizer: DomSanitizer,
              private iconRegistry:MatIconRegistry) { 
    this.avatar = localStorage.getItem('avatar');
    if(loginservice.loggedIn()){
      this.disabled=true;
    }

    this.iconRegistry.addSvgIcon('morevert',sanitizer.bypassSecurityTrustResourceUrl('assets/maticon/more_vert.svg'));

   }


  ngOnInit() {
    //console.log("in oninit() ");
  }

  frmNow(d){
    return moment(d).startOf('minute').fromNow();
  }

  getPostimg(imgpath){
    if(imgpath != undefined)
    //console.log(imgpath);
     return (this.getaImageURL+imgpath) || async; 
  }

  countRate(c,rv){

    if(rv==0)
      this.rcount = c;
    else
      this.rcount = c + 1;

    return this.rcount;
  }

  setscore(rt,rv){
    //console.log(rt);
    this.rtotal = rt;
    return rt+rv;
   }

  rateIt(rt,rv,pid){
    if(rv>=1){
    rt=rt+rv;
    //check for user if already rated it.
    var r = new Object({
        p_id:pid,
        rtotal:rt,
        rate: rv
    });

    console.log(r);
    this.postservice.ratePost(r).subscribe(res=>{
      if(res.message){
        this.setscore(res.output.total,res.output.Ruser.score);
        this.snackbr.open(res.output.Ruser.name+" have given "+res.output.Ruser.score+" Score to the post", "Ok", {
          duration: 800,
        });
      }
      else{
        //console.log(res);
        this.snackbr.open("Something went wrong", "Cancel", {
          duration: 800,
        }); 
      }
    });
    }

  }


  setCurimg(imgindex){
    this.curimg = imgindex;
  }


  showComments(pid){
    this.showComm = !this.showComm;
    this.postservice.getComments(pid).subscribe(res=>{
        if(res.found)
            this.allcomments = res.comments;
    })
    //this.allcomments = comms.sort((a,b)=> {return <any>new Date(b.time) - <any>new Date(a.time);});
  }

  getProfimg(imgpath){
    if(imgpath != undefined)
     return (this.getaImageURL+imgpath) || async; 
  }

  sendComment(comfrm,postid,comms){
     //console.log(comfrm.value.cominput);
    if(comfrm.value.cominput == null){
        this.snackbr.open("Enter text", "Cancel", {
          duration: 800000,
        });
      }
    else  if(comfrm.value.cominput.trim()==""){
      this.snackbr.open("Enter text", "Cancel", {
        duration: 800000,
      });
      comfrm.reset();
      }
    else{
      let combody:Object = {
          p_id:postid,
          cbody:comfrm.value.cominput
      };
      if(!this.showComm)
        this.showComments(comms);
      
      //here will api subscribe
      this.postservice.addComment(combody).subscribe(res =>{
        if(res.message === "Comment added"){
          comfrm.reset();
          this.allcomments.unshift(res.nc);
          this.snackbr.open("Comment added", "Ok", {
            duration: 700,
          });

        }else{
          
          this.snackbr.open(res.message, "Cancel", {
            duration: 700,
          });
        }
      });
    }
  }

}
