@Component({
  selector: 'create-contest-dialog',
  templateUrl: 'create-contest-dialog.html',
  styleUrls: ['contest.component.css']
})
export class CreateContestDialog {

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  filteredDivisions: Observable<string[]>;
  divisions: string[] = [];
  allDivs: string[] = ['WTF', 'LOL','Trolls',
  'School',
  'Sci-fi',
  'Sports',
  'Tik-Tok',
  'Animals',
  'Relationship',
  'Gaming',
  'Movies & Music',
  'Anime',
  'Cosplay',
  'K-Pop',
  'Vintage','other','Drawing',
  'Digital-Art',
  'Painting',
  'Design',
  'Illustrations',
  'Templates',
  'Crafts',
  'Calligraphy',
  'Animation',
  'Photography',
  'Comics','Classic',
  'Country',
  'Dance',
  'Electronic',
  'Hip Hop',
  'Jazz',
  'Latin',
  'Opera',
  'R&B',
  'Rock','Stories',
  'Poems',
  'Quotes',
  'Blogs',
  'Reviews',
  'Confessions','Culture',
  'Fashion',
  'Food & Drinks',
  'Health & Fitness',
  'Tourism',
  'Decorating'];
  separatorKeysCodes: number[] = [ENTER, COMMA];
  datemsg:string="";
  minDate = moment(new Date()).format('YYYY-MM-DD');

  contestform = new FormGroup({
    title: new FormControl('',[Validators.required]),
    category: new FormControl('Meme',[Validators.required]),
    divctrl: new FormControl('',[Validators.required]),
    description: new FormControl('',[Validators.required]),
    startd: new FormControl('',[Validators.required]),
    endd: new FormControl('',[Validators.required]),
    eva: new FormControl('',[Validators.required])
  });
  
  @ViewChild('divisionInput') divisionInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;

  constructor(private conservice:ContestsService,
    public dialogRef: MatDialogRef<CreateContestDialog>) {
      this.filteredDivisions = this.contestform.controls.divctrl.valueChanges.pipe(
        startWith(null),
        map((divisions: string | null) => this.allDivs.slice()));
    }

    add(event: MatChipInputEvent): void {
      if (!this.matAutocomplete.isOpen) {
        const input = event.input;
        const value = event.value;
  
        // Add our Divisions
        if ((value || '').trim()) {
          this.divisions.push(value.trim());
        }
  
        // Reset the input value
        if (input) {
          input.value = '';
        }
  
        this.contestform.controls.divctrl.setValue(null);
      }
    }
  
    remove(fruit: string): void {
      const index = this.divisions.indexOf(fruit);
  
      if (index >= 0) {
        this.divisions.splice(index, 1);
      }
    }
  
    selected(event: MatAutocompleteSelectedEvent): void {
      if(!this.divisions.includes(event.option.viewValue) && this.divisions.length<=3){
        this.divisions.push(event.option.viewValue);
        this.contestform.controls.divctrl.setValue(this.divisions);

        //console.log('in if'+this.divisions);
      }
      console.log(this.divisions);
      this.divisionInput.nativeElement.value = '';
      this.contestform.controls.divctrl.setValue(null);
    }
  
    // private _filter(value: string): string[] {
    //   const filterValue = value.toLocaleLowerCase();
  
    //   return this.allDivs.filter(divisions => divisions.toLocaleLowerCase().indexOf(filterValue) === 0);
    // }    
  
    //closing create contest dialog
  onNoClick(): void {
    this.dialogRef.close();
  }

  checkDate(event){
    var curdate =  new Date();
    var mcdate = moment(curdate);
    var msdate = moment(this.contestform.controls.startd.value);
    var medate = moment(this.contestform.controls.endd.value);
  
    console.log("sdate "+this.contestform.controls.startd.value);
    console.log("edate "+this.contestform.controls.endd.value);

    if(this.contestform.controls.startd.value.toString()==this.contestform.controls.endd.value.toString()){
      this.datemsg = "Date entered cannot be same";
      this.contestform.controls.startd.setValue('');
      this.contestform.controls.endd.setValue('');
    }

    if(medate.diff(msdate,'days')<0){
      
      //console.log(msdate);
      this.datemsg = "Date entered is not valid";
      this.contestform.controls.startd.setValue('');
      this.contestform.controls.endd.setValue('');
    }else
    this.datemsg = "";


  }

  createContest(){
    this.contestform.controls.divctrl.setValue(this.divisions);
    var sdate:String = this.contestform.controls.startd.value;
    var edate:String = this.contestform.controls.endd.value;
    var evalv = moment(this.contestform.controls.endd.value).add(this.contestform.controls.eva.value,"days").toDate();
    
  
    if(this.contestform.valid){
      
    var contest={
        ct:this.contestform.controls.category.value,
        div: this.contestform.controls.divctrl.value,
        cbodyTitle:this.contestform.controls.title.value,
        cbodyText:this.contestform.controls.description.value,
        r:"",
        cstart:sdate.toString(),
        cend:edate.toString(),
        eval:evalv.toString()
    };

    this.conservice.createcontest(contest).subscribe(res=>{
          if(res.message=="Contest Created"){
            console.log("Contest Created");
            console.log(res.contest);
            this.onNoClick();
            
          }else{
            console.log("Something went wrong Contest not Created");            
          }
    });
    //console.log(contest);
    }else
      console.log(this.contestform.errors);
      
  }

}



//Submit Entry
@Component({
  selector: 'submit-entry',
  templateUrl: 'submit-entry.html',
})
export class SubmitEntry {

  flag:Number=0;
  selectedFile:Array<File>=[];
  imgSrc=[];
  enterstatus:Boolean=true;

  entryForm = new FormGroup({
    title: new FormControl('',[Validators.required]),
    attachment: new FormControl('',[Validators.required])
  });

  constructor(
    private cs: ContestsService,
    public router: Router,
    public dialogRef: MatDialogRef<SubmitEntry>,
    @Inject(MAT_DIALOG_DATA) public data) {
      var coid = {
        "conid":this.data.conid
      }
        this.cs.subcheck(coid).subscribe(res=>{
          if(res.length>0){
            this.enterstatus=false;
          }
        })
    }

  onNoClick(): void {
    this.dialogRef.close();
  }

  selectPostImg(event){
    if (event.target.files) {
      var fs = event.target.files;
      
      for (var i=0;i<fs.length;i++) {
        var reader = new FileReader();
        //console.log(i);
        reader.onload = (e:any) => { // called once readAsDataURL is completed
          if(this.imgSrc.length < 6)
            this.imgSrc.push(e.target.result);
          //console.log(e.target);
        }

        reader.readAsDataURL(fs[i]); // read file as data url
        if(this.selectedFile.length < 6)
          this.selectedFile.push(<File> fs[i]);
      }
      console.log(this.selectedFile);
    }
  }

  showIndex(i){
    this.selectedFile.splice(i,1);
    this.imgSrc.splice(i,1);
  }

  submitEntry(){
    if(this.entryForm.valid){
    let fd = new FormData();
        fd.append('title',this.entryForm.value.title);
        fd.append('conid',this.data.conid);
        if(this.selectedFile){
        for(let i =0; i < this.selectedFile.length; i++){
          fd.append("entrymedia[]", this.selectedFile[i], this.selectedFile[i]['name']);
        }
      }
      
    this.cs.submission(fd).subscribe((res)=>{
      console.log(res);
      if(res.message==="Submission Done"){
        this.router.navigate(['/contest/id',this.data.conid]); 
        this.onNoClick();
      }
    });
  }
  }

}