import { Component, OnInit, AfterViewInit, AfterContentChecked } from '@angular/core';
import { ContestsService } from "../services/contests.service";
import * as moment from 'node_modules/moment';

@Component({
  selector: 'app-right-nav',
  templateUrl: './right-nav.component.html',
  styleUrls: ['./right-nav.component.css']
})
export class RightNavComponent implements OnInit {

  contestList;
  status:String="Open";
  timestatus:String="Starts";

  constructor(private conservice:ContestsService) {
      conservice.getContests().subscribe(
                      res=>{
                        this.contestList = res.contests;
                      }
      );
  }

  dateStr(d){
    var returnstr="Starts ";
    var curdate =  new Date().getDate();
    var strdate = new Date(d).getDate();
    if((curdate-strdate)>1){
      returnstr = "Ended ";
    }
    return returnstr+moment(d).endOf('minute').fromNow();
  }

  ngOnInit() {
      this.dateStr(null);
  }

}
