import { Component, OnInit,Renderer2 } from '@angular/core';
import { UserinfoService } from '../services/userinfo.service';
import { LoginService } from '../services/login.service';
import { Router, ActivatedRoute } from "@angular/router";
import { async } from '@angular/core/testing';
import { PostRService } from '../services/post-r.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  userinfo:Object ={};
  uposts:Array<Object>=[];
  snapshotParam:String;

  private getaImageURL="";

  constructor(private userservice: UserinfoService,
              private postservice: PostRService,
              private router:Router,
              private route:ActivatedRoute,
              private ls:LoginService ) { 

     
            
  }

  ngOnInit() {
       //geting user details    
      //  this.snapshotParam = this.route.snapshot.paramMap.get("user");
      //  console.log(this.snapshotParam);
       this.route.params.subscribe(params=>{
         this.userservice.getotheruser(params.user).subscribe(res=>{
             
           if(res.message==="User Found"){
             this.userinfo =res.user;
             var uname = new Object({
               name:res.user.username
             });
             console.log(uname);
             //geting all post of current user
             this.postservice.getaPost(uname).subscribe(resposts=>{
             if(resposts.message){
               this.uposts = resposts.pu;
               console.log(resposts);
             }
             else{
               console.log(resposts);
             }
             });
           }
           else{
             //console.log(res);
             this.ls.logoutUser();
             this.router.navigate(['/login']);
           }
         });
       });
  }

  getUserimage(imgpath){
    if(imgpath != undefined)
     return (this.getaImageURL+imgpath) || async; 
  }

}
