import { Injectable } from '@angular/core';
import { Router } from "@angular/router";
import { map } from "rxjs/operators";
import { Http,Headers,RequestOptions } from "@angular/http";
import { ReadVarExpr } from '@angular/compiler';

@Injectable({
  providedIn: 'root'
})
export class ContestsService {

  private getAllContest="contests/all";
  private getAllauthContest="contests/all/auth";
  private addContest="contests/add";
  private subs="contests/entry/add";
  private subchk="contests/entry/add/checklist";
  private addCommentURL='contests/comment/add';
  private addReplyURL='contests/reply/add';
  private getAContest="contests/id/";
  private rateItURL="contests/subs/rate";

  constructor(private http:Http, private router:Router) { }

  createcontest(contestDetails){

    console.log(contestDetails);

    let header = new Headers();
    header.append('Content-type','application/json');
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);

    let options = new RequestOptions({ headers: header });

      return this.http.post(this.addContest,contestDetails,options)
                      .pipe(map(res=> res.json()));
  }

  submission(fd){
    let header = new Headers();
    //header.append('Content-type','application/form-data');
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);
    
    let options = new RequestOptions({ headers: header });

      return this.http.post(this.subs,fd,options)
                      .pipe(map(res=> res.json()));
  }

  subcheck(conid){
    let header = new Headers();
    header.append('Content-type','application/json');
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);
    
    let options = new RequestOptions({ headers: header });

      return this.http.post(this.subchk,conid,options)
                      .pipe(map(res=> res.json()));
  }

  getauthContests(){
    
    let header = new Headers();
    header.append('Content-type','application/json');
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);

    let options = new RequestOptions({ headers: header });

      return this.http.get(this.getAllauthContest,options)
      .pipe(map(res=> res.json()));
  }

  getContests(){
    
    let header = new Headers();
    header.append('Content-type','application/json');
    let options = new RequestOptions({ headers: header });

      return this.http.get(this.getAllContest,options)
      .pipe(map(res=> res.json()));
  }

  getSingleContest(conid){
    
    let header = new Headers();
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);
    let options = new RequestOptions({ headers: header });

      return this.http.get(this.getAContest+conid,options)
      .pipe(map(res=> res.json()));
  }

  addComment(combody){
    let header = new Headers();
    header.append('Content-type','application/json');
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);

    let options = new RequestOptions({ headers: header });

      return this.http.post(this.addCommentURL,combody,options)
                      .pipe(map(res=> res.json()));
  }

  addReply(rpbody){
    let header = new Headers();
    header.append('Content-type','application/json');
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);

    let options = new RequestOptions({ headers: header });

      return this.http.post(this.addReplyURL,rpbody,options)
                      .pipe(map(res=> res.json()));
  }

  ratePost(sid,cid,rv,r){
    var sb = new Object({
        conid: cid,
        subid:sid,
        ratevalue:rv,
        rate:r
    });
    //console.log(pb);
    let header = new Headers();
    header.append('Content-type','application/json');
    header.append('Authorization',`Bearer ${localStorage.getItem('token')}`);

    let options = new RequestOptions({ headers: header });


    return this.http.post(this.rateItURL,sb,options)
                    .pipe(map(res=>res.json()));
  }


}
